import React, { Component } from 'react';
import classes from './Filter.css';
import RoundButton from '../../components/UI/RoundButton/RoundButton';


class Filter extends Component {
    render () {
        return(
            <div className={classes.Filter}>
                <select name="test">
                <option value="aa">男甲个人单打</option>
                <option value="bb">男乙个人单打</option>
                <option value="cc">男丙个人单打</option>
                <option value="dd">男丁个人单打</option>
                </select>
                <RoundButton 
                name="循环赛"
                btnType={this.props.cycleBtnStyle}
                clicked={this.props.showCycle}/>
                <RoundButton 
                name="淘汰赛"
                btnType={this.props.drawBtnStyle}
                clicked={this.props.showDraw}/>
                <RoundButton 
                name="附加赛"/>
                
            </div>
        );
    }
}

export default Filter;


